import React from 'react'

import { len } from '../../../mods/a_utils'

import TaskPlot from '../../../components/Plot/TaskPlot'
import CombinedPlot from '../../../components/Plot/CombinedPlot'

import { Tooltip } from '@mui/material'
import InfoIcon from '@mui/icons-material/Info'

import { PrimaryButton, SecondaryButton } from 'src/styled'
import { OptimizationContainer, OptimizationCharts } from './styled'

const calculatePercentage = (optimized, baseline) => {
  const diff = (optimized - baseline)
  const percentage = ((diff / baseline) * 100)
  const checkPositive = Math.sign(percentage)
  if (checkPositive === 1) {
    return <span className='badge minus'>{(Math.round(percentage))}%</span>
  }
  else if (checkPositive === -1) {
    return <span className='badge plus'>{(Math.abs(Math.round(percentage)))}%</span>
  }
  else { return <span className='badge'>{(Math.round(percentage))}%</span> }
}

export default function OptimizedSummary({ optimizer, selectedPreset, selectedResultIndex, goPrev, goNext }) {

  console.log(selectedResultIndex)

  const baselineResources = optimizer?.baseline_result?.inp?.rsrcs
  const newResources = optimizer?.results?.at(selectedResultIndex)?.inp?.rsrcs

  const resourcesData =
  {
    labor: {
      base: [],
      new: []
    },
    material: {
      base: [],
      new: []
    }
  }

  if (newResources) {
    Object.keys(newResources).forEach((l) => {
      if (newResources[l].type.toLowerCase() === 'labor') {
        resourcesData.labor.new.push(newResources[l])
      }
      if (newResources[l].type.toLowerCase() === 'material') {
        resourcesData.material.new.push(newResources[l])
      }
    })
  }

  Object.keys(baselineResources).forEach((l) => {
    if (baselineResources[l].type.toLowerCase() === 'labor') {
      resourcesData.labor.base.push(baselineResources[l])
    }
    if (baselineResources[l].type.toLowerCase() === 'material') {
      resourcesData.material.base.push(baselineResources[l])
    }
  })

  return <OptimizationContainer>
    <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Optimized Summary </h2>
    <div className='summary'>
      <div className='stats'>
        <div className='title'>Project Duration (days)
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(selectedResultIndex)?.drtn_day)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.drtn_day)}</div>
        </div>
        {calculatePercentage(optimizer?.results?.at(selectedResultIndex)?.drtn_day, optimizer?.baseline_result?.drtn_day)}
      </div>
      <div className='stats'>
        <div className='title'>Resources
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(selectedResultIndex)?.rsrc_max)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.rsrc_max)} </div>
        </div>
        {calculatePercentage(optimizer?.results?.at(selectedResultIndex)?.rsrc_max, optimizer?.baseline_result?.rsrc_max)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks <Tooltip title='Tasks that were changed'><InfoIcon /></Tooltip>
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(selectedResultIndex)?.num_changed)} </div>
          <div className='baseline-stats'>{Math.round(Object.values(optimizer?.baseline_result?.inp?.tasks).length)} </div>
        </div>
        {calculatePercentage(Math.round(Object.values(optimizer?.baseline_result?.inp?.tasks).length) - optimizer?.results?.at(selectedResultIndex)?.num_changed, Object.values(optimizer?.baseline_result?.inp?.tasks).length)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks on Critical Path
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results[optimizer?.results?.length - 1]?.num_crt)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.num_crt)}  </div>
        </div>
        {calculatePercentage(optimizer?.results.at(selectedResultIndex)?.num_crt, optimizer?.baseline_result?.num_crt)}
      </div>
    </div>
    {optimizer?.results?.length > 0 ?
      <OptimizationCharts className="multi-charts">
        <div className='full-chart'>
          <h3>Tasks (Overview)</h3>
          <span className="subtitle">Overview of all tasks over time</span>
          <div className='legend'>
            <div className="item">
              <div style={{ width: '14px', height: '14px', background: '#82B5C7', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Optimized</div>
            </div>
            <div className="item">
              <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Baseline</div>
            </div>
            {len(optimizer.results.at(-1)?.inp?.tasks || {}) <= 400 ?
              <div className="item">
                <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                <div>Critical Path</div>
              </div>
              : <></>
            }
          </div>
          {optimizer.results[optimizer.results.length - 1]?.inp?.tasks ?
            <TaskPlot
              width="1150"
              height="400"
              tasks={optimizer.results?.at(selectedResultIndex)?.inp.tasks}
              baselineTasks={optimizer.baseline_result?.inp.tasks}
              title='Tasks (Overview)' />
            : 'No data'
          }
        </div>
        <div className='full-chart'>
          <div className='row'>
            <div>
              <h3>Labor Resources</h3>
              <span className="subtitle">Total utilization of multiple resources over time</span>
            </div>
          </div>
          {optimizer.results?.at(selectedResultIndex)?.rsrc_dist ?
            <CombinedPlot
              width="1150"
              base_results={resourcesData.labor.base}
              new_results={resourcesData.labor.new}
              base_resource_distribution={optimizer?.baseline_result?.rsrc_dist}
              new_resource_distribution={optimizer?.results?.at(selectedResultIndex)?.rsrc_dist}
              filter='Labor'
              title='Combined Resources' />
            : 'No data'
          }
        </div>

        {resourcesData?.material?.base?.length === 0 ? '' : <>
          <div className='full-chart'>
            <div className='row'>
              <div>
                <h3>Material Resources</h3>
                <span className="subtitle">Total utilization of multiple resources over time</span>
              </div>
            </div>
            {optimizer.results?.at(selectedResultIndex)?.rsrc_dist ?
              <CombinedPlot
                width="1150"
                base_results={resourcesData.material.base}
                new_results={resourcesData.material.new}
                base_resource_distribution={optimizer?.baseline_result?.rsrc_dist}
                new_resource_distribution={optimizer?.results?.at(selectedResultIndex)?.rsrc_dist}
                filter='Material'
                title='Combined Resources' />
              : 'No data'
            }
          </div></>}

      </OptimizationCharts> : ''}
    <div className='row'>
      <SecondaryButton variant='contained' onClick={goPrev}>
        Back
      </SecondaryButton>
      <PrimaryButton variant='contained' onClick={goNext}>
        View Detailed Report
      </PrimaryButton>
    </div>
  </OptimizationContainer>
}