import React, { useState, useRef } from 'react'

import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import { useSignIn } from 'react-auth-kit'
import { useNavigate } from 'react-router-dom'

import { login } from '../../lib/fetch'

import { validateEmail } from '../../utils/validator'
import { LoginContainer } from './styled'

import FLlogo from '../../assets/images/logo.png'
import { PrimaryButton } from 'src/styled'

export default function Login() {
  const emailRef = useRef()
  const [emailErrorMessage, setEmailErrorMessage] = useState('')
  const [errorMessage, setErrorMessage] = useState('')

  const passwordRef = useRef()
  const [passwordErrorMessage, setPasswordErrorMessage] = useState('')

  const signIn = useSignIn()
  const navigate = useNavigate()

  async function handleLogin() {
    const validEmail = validateEmail(emailRef.current.value)
    setEmailErrorMessage(validEmail.message)
    if (!validEmail.result) { return }

    const response = await login({
      email: emailRef.current.value,
      password: passwordRef.current.value,
    })
      .then((response) => {
        return response.json()
      })
      .then((data) => {
        if (data.resetPasswordRequired) {
          // setErrorMessage('Your account has no password, you will be taken to reset password page')
          setErrorMessage('This is your first time logging in, you will be redirected to set up a password.')
          navigate('/#/reset-password?email=' + emailRef.current.value)
          return
        } else if (data.accessToken) {
          signIn({
            token: data.accessToken,
            expiresIn: 24 * 60, // in minutes -> 1 day
            tokenType: 'Bearer',
            authState: { email: emailRef.current.value },
          })
          navigate('/')
          return
        } else {
          setErrorMessage(data.message)
          return
        }
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }

  function handleForgotPassword() {
    navigate('/reset-password?email=' + emailRef.current.value)
  }

  return (
    <LoginContainer>
      <Box
        sx={{ display: 'flex', flexDirection: 'column', marginRight: '40px' }}
      >
        <img className='logo' src={FLlogo} alt='Frontline Logo' />
        <h1>Optimization Platform</h1>
        Find the best sequence of activities with
        optimal resource loading to ace your project
      </Box>
      <Box className='form' component='form' noValidate autoComplete='off'>
        <h2>Log In</h2>
        <p className='error'>{errorMessage}</p>
        <TextField
          label='Email'
          variant='outlined'
          inputRef={emailRef}
          error={emailErrorMessage !== ''}
          helperText={emailErrorMessage}
          placeholder='sample@googlemail.com'
        />
        <TextField
          label='Password'
          type='password'
          variant='outlined'
          inputRef={passwordRef}
          error={passwordErrorMessage !== ''}
          helperText={passwordErrorMessage}
          placeholder='******'
        />
        <div className='row'>
          <span style={{ textDecoration: 'underline', cursor: 'pointer' }} onClick={handleForgotPassword}>Forgot password</span>
          <PrimaryButton variant='contained' onClick={handleLogin}>
            Login
          </PrimaryButton>
        </div><br />
        <div className='divider' />
        <h2>New Users</h2>
        Our platform is currently only available to subscribers.
        Please contact <a href='mailto:sales@frontlinec.com' target="_blank" rel="noreferrer">sales@frontlinec.com</a> to request a demo.
      </Box>
    </LoginContainer>
  )
}
