import React from 'react'

import { LoadingModal, ModalContent } from './styled'

const Modal = ({ isLoading, spinner, onClose, title, content }) => {
  return (
    <LoadingModal
      open={isLoading}
      onClose={onClose}
      aria-labelledby='modal-modal-title'
      aria-describedby='modal-modal-description'
    >
      <ModalContent>
        {spinner ? <div className='loader' /> : ''}

        <div className='title' id='modal-modal-title'>
          {title}
        </div>
        <span>{content}</span>
      </ModalContent>
    </LoadingModal>
  )
}

export default Modal