import { ProjectReader } from './ProjectReader'
import { format_date, encode_id, write_file, to_dict } from '../../../../utils/reader'

/**
 * https://docs.oracle.com/cd/F51303_01/English/Mapping_and_Schema/xer_import_export_data_map_project/
 */

export class XerReader extends ProjectReader {
  parse_text(text = '') {
    let prj = { tables: {} },
      table = [],
      keys
    text.split('\n').forEach((line) => {
      let cols = line.split('\t').map((x) => x.trim())
      let type = cols[0],
        items = cols.slice(1)
      switch (type) {
        case 'ERMHDR':
          prj.header = items
          break
        case '%T':
          table = prj.tables[cols[1]] = []
          break
        case '%F':
          keys = items
          break
        case '%R':
          table.push(to_dict(keys, items))
          break
        case '%E':
          break
        default:
          console.error(type)
          break
      }
    })
    this.raw_data = prj
  }

  extract_data() {
    let tasks = {}
    let rsrcs = {}
    let clndrs = {}

    let prj = this.raw_data.tables
    const task_id_code_map = {}

    prj.TASK.forEach((t) => {
      let id = encode_id(t.task_id)
      let name = t.task_name
      let start = Date.parse(t.target_start_date + 'Z')
      let finish = Date.parse(t.target_end_date + 'Z')
      let drtn_hr = parseFloat(t.target_drtn_hr_cnt)
      let act_start = Date.parse(t.act_start_date + 'Z')
      let act_finish = Date.parse(t.act_end_date + 'Z')
      let clndr_id = encode_id(t.clndr_id)
      const task_code = t.task_code
      task_id_code_map[id] = task_code

      tasks[id] = {
        id,
        name,
        start,
        finish,
        drtn_hr,
        act_start,
        act_finish,
        preds: {},
        rsrcs: {},
        clndr_id,
        task_code
      }
    })

    if (prj.RSRC) {
      prj.RSRC.forEach((r) => {
        let id = encode_id(r.rsrc_id)
        let name = r.rsrc_name
        let type = r.rsrc_type.slice(3)
        type = { Labor: 'Labor', Equip: 'Equipment', Mat: 'Material' }[type]
        rsrcs[id] = { id, name, type, dist: {} }
      });
    }

    prj.CALENDAR.forEach((c) => {
      let id = encode_id(c.clndr_id)
      let name = c.clndr_name
      let week_hr_cnt = parseFloat(c.week_hr_cnt)
      clndrs[id] = { id, name, week_hr_cnt }
    });

    prj.TASKPRED.forEach((x) => {
      let id = encode_id(x.task_pred_id)
      let t_id = encode_id(x.task_id)
      let p_id = encode_id(x.pred_task_id)
      let type = x.pred_type.slice(3)
      let lag_hr = parseFloat(x.lag_hr_cnt)
      tasks[t_id].preds[p_id] = {
        id,
        t_id,
        p_id,
        type,
        lag_hr,
      }
    })

    if (prj.TASKRSRC) {
      prj.TASKRSRC.forEach((x) => {
        let id = encode_id(x.taskrsrc_id)
        let t_id = encode_id(x.task_id)
        const t_code = task_id_code_map[t_id]
        let r_id = encode_id(x.rsrc_id)
        let work_hr = parseFloat(x.target_qty)
        let units = work_hr / tasks[t_id].drtn_hr

        /*let units_2 = parseFloat(x.target_qty_per_hr);
        if (units.toFixed(4) !== units_2.toFixed(4)) {
          console.log(work_hr, tasks[t_id].drtn_hr, units, units_2);
        }*/
        if (tasks[t_id].rsrcs[r_id]) {
          this.errors.add(
            `removed duplicate assignment between task \`${t_id}\` and resource \`${r_id}\``
          );
          x.DELETE = true;
          return;
        }
        tasks[t_id].rsrcs[r_id] = {
          id,
          t_id,
          t_code,
          r_id,
          work_hr,
          units,
        }
      })
      prj.TASKRSRC = prj.TASKRSRC.filter((x) => !x.DELETE)
    }

    this.data = { tasks, rsrcs, clndrs }
  }

  merge_data(inp) {
    let prj = this.raw_data.tables

    prj.TASK.forEach((t) => {
      let id = encode_id(t.task_id)
      let t2 = inp.tasks[id]
      t.target_start_date =
        t.early_start_date =
        t.late_start_date =
          format_date(t2.start_2)
      t.target_end_date =
        t.early_end_date =
        t.late_end_date =
          format_date(t2.finish_2)
      t.target_drtn_hr_cnt = t2.drtn_hr_2
    })

    if (prj.TASKRSRC) {
      prj.TASKRSRC.forEach((x) => {
        let id = encode_id(x.taskrsrc_id)
        let x2 = inp.task_rsrcs[id]
        x.target_qty_per_hr = x2.units_2
      })
    }
  }

  export() {
    let lines = []
    lines.push(['ERMHDR', ...this.raw_data.header].join('\t'))
    let prj = this.raw_data.tables
    Object.entries(prj).forEach(([key, table]) => {
      lines.push(['%T', key].join('\t'))
      lines.push(['%F', ...Object.keys(table[0])].join('\t'))
      table.forEach((row) => {
        lines.push(['%R', ...Object.values(row)].join('\t'))
      })
    })
    lines.push('%E')
    let name = this.filename.replace('.xer', '_Frontline.xer')
    write_file(name, lines.join('\n'))
  }
}