import React, { useState, useReducer } from 'react'

import { Optimizer } from './InputData/optimizers/Optimizer'

import Breadcrumb from '../../components/Breadcrumb'
import Header from '../../components/Header'

import InputData from './InputData'
import BaselineSummary from './BaselineSummary'
import OptimizedSummary from './OptimizedSummary'
import DetailedReport from './DetailedReport'

import { DashboardContainer } from './styled'

const steps = [
  'Input Data',
  'Baseline Summary',
  'Optimized Summary',
  'Optimized Report'
]

export default function Dashboard() {
  const [navAt, setNavAt] = useState(0)
  const [project, setProject] = useState(null)
  const [optimizationPresets, setOptimizationPresets] = useState({
    balanced: {
      label: 'Balanced',
      color: '#82B5C7',
      description: 'Best combination of the fastest, minimum and levelled resources'
    },
    shortest_duration: {
      label: 'Fastest',
      color: '#f7dc5b',
      description: 'Shortest project duration'
    },
    least_max_resources: {
      label: 'Minimum Resources',
      color: '#db7930',
      description: 'Least amount of required resources'
    },
    least_std_resources: {
      label: 'Levelled Resources',
      color: '#AABB5D',
      description: 'Best resource distribution'
    }
  })
  const [selectedPreset, setSelectedPreset] = useState('balanced')
  const [selectedResultIndex, setSelectedResultIndex] = useState(-1)
  const [uploadedFile, setUploadedFile] = useState()
  const [fileName, setFileName] = useState('No file chosen')
  const [constraintsFileName, setConstraintsFileName] = useState('No file chosen')

  const [_, forceUpdate] = useReducer((x) => x + 1, 0)

  function optimizePresets(project) {
    if (!project) return
    for (const op in optimizationPresets) {
      if (optimizationPresets[op]?.optimizer)
        optimizationPresets[op].optimizer.stop()
    }
    for (const op in optimizationPresets) {
      optimizationPresets[op].optimizer = new Optimizer(project?.fileReader)
      optimizationPresets[op].optimizer.sim_in = { ...project.optimizer.sim_in }

      for (const weight in optimizationPresets[op].optimizer.loss_weights)
        optimizationPresets[op].optimizer.loss_weights[weight] = 0
      if (op === 'shortest_duration')
        optimizationPresets[op].optimizer.loss_weights['drtn_day'] = 100
      else if (op === 'least_max_resources')
        optimizationPresets[op].optimizer.loss_weights['rsrc_max'] = 100
      else if (op === 'least_std_resources')
        optimizationPresets[op].optimizer.loss_weights['rsrc_std'] = 100
      else if (op === 'balanced') {
        optimizationPresets[op].optimizer.loss_weights['drtn_day'] = 50
        optimizationPresets[op].optimizer.loss_weights['rsrc_max'] = 50
      }
    }

    optimizationPresets['balanced'].optimizer.optimize((opt_res1) => {
      if (opt_res1.length === 1)
        project.optimizer.baseline_result = { ...opt_res1[0] }
      optimizationPresets['balanced'].optimizer.optimizing = true
      optimizationPresets['balanced'].optimizer.results = opt_res1
      if (selectedPreset === '')
        setSelectedPreset('balanced')
      forceUpdate()
    }, (opt_res1) => {
      optimizationPresets['balanced'].optimizer.optimizing = false
      optimizationPresets['shortest_duration'].optimizer.optimize((opt_res2) => {
        optimizationPresets['shortest_duration'].optimizer.optimizing = true
        optimizationPresets['shortest_duration'].optimizer.results = opt_res2
        forceUpdate()
      }, (opt_res2) => {
        optimizationPresets['shortest_duration'].optimizer.optimizing = false
        optimizationPresets['least_max_resources'].optimizer.optimize((opt_res3) => {
          optimizationPresets['least_max_resources'].optimizer.optimizing = true
          optimizationPresets['least_max_resources'].optimizer.results = opt_res3
          forceUpdate()
        }, (opt_res3) => {
          optimizationPresets['least_max_resources'].optimizer.optimizing = false
          optimizationPresets['least_std_resources'].optimizer.optimize((opt_res4) => {
            optimizationPresets['least_std_resources'].optimizer.optimizing = true
            optimizationPresets['least_std_resources'].optimizer.results = opt_res4
            forceUpdate()
          }, (opt_res4) => {
            optimizationPresets['least_std_resources'].optimizer.optimizing = false
            forceUpdate()
          })
        })
      })
    })
  }

  let componentToRender = null

  if (navAt === 0)
    componentToRender = <InputData
      project={project}
      fileName={fileName}
      setFileName={setFileName}
      uploadedFile={uploadedFile}
      setUploadedFile={setUploadedFile}
      constraintsFileName={constraintsFileName}
      setConstraintsFileName={setConstraintsFileName}
      updateProject={(p) => {
        setProject(p)
      }}
      goNext={() => {
        optimizePresets(project)
        setNavAt(navAt + 1)
      }} />
  else if (navAt === 1)
    componentToRender = <BaselineSummary
      optimizer={project?.optimizer}
      optimizationPresets={optimizationPresets}
      selectedPreset={selectedPreset}
      selectedResultIndex={selectedResultIndex}
      handleSelectPreset={(preset) => {
        setSelectedPreset(preset)
        if (optimizationPresets[preset].optimizer.results.length - 1 < selectedResultIndex)
          setSelectedResultIndex(optimizationPresets[preset].optimizer.results.length - 1)
      }}
      handleSelectResultIndex={(index) => { setSelectedResultIndex(index) }}
      goPrev={() => { setNavAt(navAt - 1) }}
      goNext={() => {
        setNavAt(navAt + 1)
        if (selectedResultIndex === -1)
          setSelectedResultIndex(optimizationPresets[selectedPreset].optimizer.results.length - 1)
        project.optimizer.loss_weights = { ...optimizationPresets[selectedPreset].optimizer.loss_weights }
        project.optimizer.results = [...optimizationPresets[selectedPreset].optimizer.results]
      }}
    />
  else if (navAt === 2)
    componentToRender = <OptimizedSummary
      optimizer={project?.optimizer}
      selectedPreset={optimizationPresets[selectedPreset].label}
      selectedResultIndex={selectedResultIndex}
      goPrev={() => { setNavAt(navAt - 1) }}
      goNext={() => { setNavAt(navAt + 1) }}
    />
  else if (navAt === 3)
    componentToRender = <DetailedReport
      optimizer={project?.optimizer}
      selectedPreset={optimizationPresets[selectedPreset].label}
      selectedResultIndex={selectedResultIndex}
      handleSelectResultIndex={(index) => { setSelectedResultIndex(index) }}
      goPrev={() => { setNavAt(navAt - 1) }}
      goNext={() => { setNavAt(navAt + 1) }}
    />


  const checkPresetResultsLoaded = selectedPreset && optimizationPresets[selectedPreset]?.optimizer?.results?.length === 0


  return (
    <>
      <Header />
      <Breadcrumb navAt={navAt} steps={steps} navigate={
        (step) => {
          if (navAt === 1) {
            if (selectedResultIndex === -1)
              setSelectedResultIndex(optimizationPresets[selectedPreset].optimizer.results.length - 1)
            project.optimizer.loss_weights = { ...optimizationPresets[selectedPreset].optimizer.loss_weights }
            project.optimizer.results = [...optimizationPresets[selectedPreset].optimizer.results]
          }
          setNavAt(step)
        }
      } disabled={checkPresetResultsLoaded || navAt === 0 || project === null || project?.optimizer === undefined || optimizationPresets['balanced']?.optimizer?.optimizing === undefined} />
      <DashboardContainer>
        <div className="content">
          {componentToRender}
        </div>
      </DashboardContainer>
    </>
  )
}