import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3 } from '../../utils/plot'
import { plot_color_palette } from '../../utils/color'
import { len, down_sample } from '../../mods/a_utils'

export default function TaskPlot({
  tasks,
  baselineTasks,
  width,
  height,
  color,
  keep = 400,
}) {
  const ref = useRef()
  useEffect(() => {
    if (!(baselineTasks && len(baselineTasks)) || (!(tasks) && len(tasks))) return
    if (!tasks) return

    let data = Object.values(tasks).sort((a, b) => a.start_2 - b.start_2)
    let baselineData = Object.values(baselineTasks).sort((a, b) => a.start_2 - b.start_2)
    if (data.length > keep) {
      data = data.filter(d => d.critical)
      baselineData = baselineData.filter(bd => bd.critical)
    }

    const x_optimized_domain = [
      Math.min(...data.map((d) => d.start_2)),
      Math.max(...data.map((d) => d.finish_2)),
    ]
    const x_baseline_domain = [
      Math.min(...baselineData.map((d) => d.start_2)),
      Math.max(...baselineData.map((d) => d.finish_2)),
    ]
    const x_domain = [
      Math.min(x_optimized_domain[0], x_baseline_domain[0]),
      Math.max(x_optimized_domain[1], x_baseline_domain[1]),
    ]

    // TO ONLY LOAD FIRST HALF OF CHART X-AXIS IF DURATION IS TOO LONG
    // function addDays(date, days) {
    //   date.setDate(date.getDate() + days)
    //   return date
    // }

    // function dateDiff(first, second) {
    //   return Math.round((second - first) / (1000 * 60 * 60 * 24))
    // }

    // const diff = dateDiff((new Date(x_domain[0])), (new Date(x_domain[1])))
    // const newEndDate = addDays(new Date(x_domain[0]), diff)
    //

    data = down_sample(data, keep)
    baselineData = down_sample(baselineData, keep)

    const y_optimized_domain = [data.length, 0]
    const y_baseline_domain = [baselineData.length, 0]
    const y_domain = [
      Math.max(y_optimized_domain[0], y_baseline_domain[0]),
      Math.min(y_optimized_domain[1], y_baseline_domain[1]),
    ]

    const { svg, x_scale, y_scale } = init_d3({
      margin: 40,
      x_domain,
      y_domain,
      width: width ? width : 560,
      height: height ? height : 500,
      ref,
      // x_label: 'time',
      // y_label: 'task',
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
    })

    const rect_base = svg.append('g')
    rect_base
      .selectAll('rect')
      .data(baselineData)
      .join('rect')
      .attr('x', (d) => x_scale(d.start_2))
      .attr('y', (d, i) => y_scale(i))
      .attr('width', (d) =>
        Math.max(x_scale(d.finish_2) - x_scale(d.start_2), 0)
      )
      .attr('height', y_scale(1) - y_scale(0))
      .attr('fill', (d) => (d.critical && len(baselineTasks) <= keep ? plot_color_palette.TaskPlot.baselineCriticalTask : plot_color_palette.TaskPlot.baselineTask))

    const rect = svg.append('g')
    rect
      .selectAll('rect')
      .data(data)
      .join('rect')
      .attr('x', (d) => x_scale(d.start_2))
      .attr('y', (d, i) => y_scale(i))
      .attr('width', (d) =>
        Math.max(x_scale(d.finish_2) - x_scale(d.start_2), 0)
      )
      .attr('height', y_scale(1) - y_scale(0))
      .attr('fill', (d) => (d.critical && len(tasks) <= keep ?
        plot_color_palette.TaskPlot.optimizedCriticalTask
        : color ? color : plot_color_palette.TaskPlot.optimizedTask))


  }, [tasks])

  if (!(baselineTasks && len(baselineTasks)) || (!(tasks) && len(tasks))) return null

  const msg =
    len(tasks) > keep
      ? 'showing up to 400 critical tasks only due to limited pixels'
      : ''
  return (
    <>
      <>{msg}</>
      <svg ref={ref}></svg>
    </>
  )
}