import { copy, Counter } from '../../../../utils/reader'
import { read_excel, write_excel } from '../../../../utils/excel'
import { format_fields } from '../../../../utils/format'

export class ProjectReader {
  errors = new Counter()

  async read_from_file(file = new File()) {
    this.filename = file.name
    let reader = new FileReader()
    return new Promise((res) => {
      reader.onload = (e) => {
        let text = e.target.result.trim()
        this.parse_text(text)
        this.extract_data()
        this.cons_data = this.make_constraints()

        res(true)
      }
      reader.readAsText(file)
    })
  }

  parse_text() { }
  extract_data() { }

  merge_data() { }
  export() { }

  export_excel(inp) {
    let { tasks, rsrcs, clndrs, task_preds, task_rsrcs } = copy(inp)

    const copy_tasks = copy(tasks)

    for (let g of [tasks, rsrcs, clndrs, task_preds, task_rsrcs]) {
      Object.values(g).forEach((x) => {
        Object.keys(x).forEach((k) => {
          x[k] = format_fields(k, x[k])
        })
      })
    }

    Object.keys(tasks).forEach((t) => {
      if (copy_tasks[t].finish < Date.now()) {
        tasks[t].act_start = tasks[t].start
        tasks[t].start_2 = tasks[t].start
        tasks[t].act_finish = tasks[t].finish
        tasks[t].finish_2 = tasks[t].finish
      }
    })

    Object.values(tasks).forEach((t) => {
      if (t.task_code) {
        t.id = t.task_code
        delete t.task_code
      }
    })

    Object.values(task_preds).forEach((t) => {
      if (inp.tasks[t.t_id].task_code)
        t.t_id = inp.tasks[t.t_id].task_code
      if (inp.tasks[t.p_id].task_code)
        t.p_id = inp.tasks[t.p_id].task_code
    })

    Object.values(task_rsrcs).forEach((t) => {
      if (t.t_code) {
        t.t_id = t.t_code
        delete t.t_code
      }
    })

    let name = this.filename
      .replace('.xml', '_Frontline.xlsx')
      .replace('.xer', '_Frontline.xlsx')
    write_excel({
      book: { tasks, rsrcs, clndrs, task_preds, task_rsrcs },
      filename: name,
    })
  }

  make_constraints() {
    let tasks_cons = [],
      rsrcs_cons = [],
      task_rsrc_cons = []

    Object.values(this.data.tasks).forEach((t) => {
      let { id, name, drtn_hr, clndr_id } = t
      tasks_cons.push({
        id,
        name,
        drtn_hr,
        min_drtn_hr: null,
        max_drtn_hr: null,
        week_hr_cnt: this.data.clndrs[clndr_id].week_hr_cnt,
        ignored: null
      })

      Object.values(t.rsrcs).forEach((x) => {
        let { id, t_id, r_id, units } = x
        task_rsrc_cons.push({
          id,
          t_id,
          r_id,
          t_name: name,
          r_name: this.data.rsrcs[r_id].name,
          units,
          min_units: null,
          max_units: null,
        })
      })
    })

    Object.values(this.data.rsrcs).forEach((r) => {
      let { id, name, baseline_resource } = r
      rsrcs_cons.push({
        id,
        name,
        max_units_per_day: null,
        cost_per_unit_hr: null,
        baseline_resource: baseline_resource ? baseline_resource : null
      })
    })

    return { tasks_cons, rsrcs_cons, task_rsrc_cons }
  }

  export_constraints() {
    let name = this.filename
      .replace('.xml', '_Frontline_Cons.xlsx')
      .replace('.xer', '_Frontline_Cons.xlsx')

    const book = this.make_constraints()

    Object.values(book.tasks_cons).forEach((t) => {
      if (this.data.tasks[t.id].task_code)
        t.id = this.data.tasks[t.id].task_code
    })

    Object.values(book.task_rsrc_cons).forEach((t) => {
      if (this.data.tasks[t.t_id].task_code)
        t.t_id = this.data.tasks[t.t_id].task_code
    })
    write_excel({ book: book, filename: name })
  }

  async read_constraints(file) {
    this.cons_filename = file.name
    this.cons_data = await read_excel(file)

    const book = this.make_constraints()

    for (let i = 0; i < this.cons_data.tasks_cons.length; i++)
      this.cons_data.tasks_cons[i].id = book.tasks_cons[i].id

    for (let i = 0; i < this.cons_data.task_rsrc_cons.length; i++)
      this.cons_data.task_rsrc_cons[i].t_id = book.task_rsrc_cons[i].t_id
  }
}