import * as xlsx from 'xlsx'

export const ms_per_hr = 60 * 60 * 1000
export const ms_per_day = 24 * ms_per_hr
export const ms_per_week = 7 * ms_per_day

export function encode_id(id) { return 'id_' + id }
export function len(x) { return x && Object.keys(x).length }
export function copy(x) { return JSON.parse(JSON.stringify(x)) }

export function parse_drtn_hr(x = 'PT200H0M0S') {
  return parseFloat(x.split('PT')[1].split('H')[0])
}

export function recover_drtn(drtn_hr) {
  return 'PT' + drtn_hr + 'H0M0S'
}

export function parse_time(x = '07:00:00') {
  return Date.parse('1971-06-28T' + x)
    - Date.parse('1971-06-28T00:00:00')
}

export function to_iso(timestamp) {
  return new Date(timestamp).toISOString().split('.')[0]
}

export function fmt_date(date) {
  return new Date(date).toISOString().replace('T', ' ').slice(0, 16)
}

export function export_excel({ book, filename }) {
  let wb = xlsx.utils.book_new()
  Object.entries(book).forEach(([name, data]) => {
    let ws = xlsx.utils.json_to_sheet(Object.values(data))
    xlsx.utils.book_append_sheet(wb, ws, name)
  })
  xlsx.writeFile(wb, filename)
}

export function read_excel(file) {
  return new Promise((res) => {
    let reader = new FileReader()
    reader.onload = e => {
      let book = xlsx.read(e.target.result, { type: 'binary' })
      let data = {}
      book.SheetNames.forEach(name => {
        data[name] = xlsx.utils.sheet_to_json(book.Sheets[name], {
          //raw: true,
        })
      })
      res(data)
    }
    reader.readAsBinaryString(file)
  })
}

export function to_dict(keys = [], values = []) {
  let dic = {}
  keys.forEach((k, i) => dic[k] = values[i])
  return dic
}

export function export_file(filename, text) {
  let elm = document.createElement('a')
  let prefix = 'data:text/plain;charset=utf-8,'
  elm.setAttribute('href', prefix + encodeURIComponent(text))
  elm.setAttribute('download', filename)
  elm.style.display = 'none'
  document.body.appendChild(elm)
  elm.click()
  document.body.removeChild(elm)
}

export function down_sample(data_, keep) {
  let data = []
  if (data_.length > keep) {
    let skip = Math.floor(data_.length / keep)
    for (let i = 0; i < keep; i++) {
      data.push(data_[i * skip])
    }
  }
  else data = data_
  return data
}

export function sleep(ms) {
  return new Promise((res, rej) => {
    setTimeout(() => { res() }, ms)
  })
}