export function validateEmail(email) {
  const re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i
  const result = re.test(String(email).toLowerCase())
  if (result) { return { result: result, message: '' } }

  return { result: result, message: 'Please enter a valid email address.' }
}

export function validatePassword(password) {
  if (password.length < 8)
  	return { result: false, message: 'Password must be 8 characters or longer.' }

  const upperReg = /[A-Z]+/
  if (upperReg.test(password))
    return { result: false, message: 'Password must contain an uppercase letter.' }

  const lowerReg = /[A-Z]+/
  if (lowerReg.test(password))
    return { result: false, message: 'Password must contain a lowercase letter.' }

  const numberReg = /[0-9]+/
  if (numberReg.test(password))
    return { result: false, message: 'Password must contain a number.' }

  return { result: true, message: '' }
}