import React from 'react'

import { Step, StepLabel } from '@mui/material'

import { BreadcrumbContainer } from './styled'

export default function Breadcrumb({ steps, navAt, navigate, disabled }) {
  return <BreadcrumbContainer activeStep={navAt} alternativeLabel>
    {steps.map((label, i) => (
      <Step key={label} onClick={() => {
        if (!disabled)
          navigate(i)
      }}>
        <StepLabel>{label}</StepLabel>
      </Step>
    ))}
  </BreadcrumbContainer>
}
