export function copy(x) {
    return JSON.parse(JSON.stringify(x))
}

export function format_date(date) {
    if (!date) return ''
    return new Date(date).toISOString().replace('T', ' ').slice(0, 16)
}

// used by MspReader.js

export function parse_drtn_hr(x = 'PT200H0M0S') {
    return parseFloat(x.split('PT')[1].split('H')[0])
}

export function recover_drtn(drtn_hr = 1) {
    return 'PT' + drtn_hr + 'H0M0S'
}

export function parse_time(x = '07:00:00') {
return Date.parse('1971-06-28T' + x) - Date.parse('1971-06-28T00:00:00')
}

export function to_iso(timestamp) {
    return new Date(timestamp).toISOString().split('.')[0]
}

export const ms_per_hr = 60 * 60 * 1000
export const ms_per_day = 24 * ms_per_hr
export const ms_per_week = 7 * ms_per_day

export function encode_id(id) {
return 'id_' + id
}

export function write_file(filename, text) {
    let elm = document.createElement('a')
    let prefix = 'data:text/plaincharset=utf-8,'
    elm.setAttribute('href', prefix + encodeURIComponent(text))
    elm.setAttribute('download', filename)
    elm.style.display = 'none'
    document.body.appendChild(elm)
    elm.click()
    document.body.removeChild(elm)
}

export function as_array(x) {
    if (!x) return []
    if (!Array.isArray(x)) return [x]
    return x
}

// used by XerReader.js

export function to_dict(keys = [], values = []) {
    let dic = {}
    keys.forEach((k, i) => (dic[k] = values[i]))
    return dic
}

// used by ProjectReader.js

export class Counter {
    constructor() {
    this.dic = {}
}

get size() {
    return len(this.dic)
}

get list() {
    return Object.entries(this.dic).map(
     ([value, cnt]) => `${value} (count: ${cnt})`
    )
}

add(value) {
    if (!(value in this.dic)) {
        this.dic[value] = 0
    }
    this.dic[value] += 1
}
}

export function len(x) {
    return Object.keys(x).length;
}