import { format_date, len } from './reader'

export function format_fields(k = '', v) {
  if (typeof v === 'string') return v

  if (k.includes('start') || k.includes('finish')) return format_date(v)

  if (typeof v === 'number') {
    if (k.includes('ratio')) return v.toFixed(2)
    return v.toFixed(0)
  }

  if (v === null || v === false) return ''
  if (Array.isArray(v) && v.length < 3) return JSON.stringify(v)
  if (typeof v === 'object') return len(v)

  return JSON.stringify(v)
}

export function limitString(str, limit) {
  if (!str) return ''
  if (str.length <= limit) return str
  
  return str.substring(0,limit) + '...'
}