import React, { useState } from 'react'
import { useSearchParams } from 'react-router-dom'

import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'

import { requestResetPassword, resetPassword } from '../../lib/fetch'
import { useNavigate } from 'react-router-dom'

import FLlogo from '../../assets/images/logo.png'

import { validateEmail } from '../../utils/validator'
import { LoginContainer } from '../Login/styled'

import { ResetContainer } from './styled'
import { PrimaryButton, SecondaryButton } from 'src/styled'

export default function ResetPassword() {
  const [searchParams, setSearchParams] = useSearchParams()
  const [email, setEmail] = useState(searchParams.get('email') ? searchParams.get('email') : '')
  const [password, setPassword] = useState('')
  const [resetToken, setResetToken] = useState(searchParams.get('resetToken'))

  const [errorMessage, setErrorMessage] = useState('')

  const navigate = useNavigate()

  async function handleRequestResetPassword() {
    const validEmail = validateEmail(email)
    setErrorMessage(validEmail.message)
    if (!validEmail.result) { return }

    const response = await requestResetPassword({ email: email })
      .then(response => { return response.json() })
      .then((data) => {
        setErrorMessage('We have sent the password reset instructions to your email. Please note the link expires in 1 hour.')
      })
      .catch((error) => { console.error('Error:', error) })
  }

  async function handleResetPassword() {
    const response = await resetPassword({ email: email, password: password, token: resetToken })
      .then(response => { return response.json() })
      .then((data) => {
        setErrorMessage('Your password has been successfully reset!')
        navigate('/')
      })
      .catch((error) => { console.error('Error:', error) })
  }

  function handleBackPage() {
    navigate('/login')
  }

  if (resetToken)
    return (
      <LoginContainer>
        <Box
          sx={{ display: 'flex', flexDirection: 'column', marginRight: '40px' }}
        >
          <img className='logo' src={FLlogo} alt="Frontline Logo" />
          <h1>Optimization Platform</h1>
          Find the best sequence of activities with
          optimal resource loading to ace your project
        </Box>
        <Box className='form' component='form' noValidate autoComplete='off'>
          <h2>Reset Password</h2>
          {errorMessage}
          <p>Your new password must be different from previous used passwords.</p>
          <TextField label='Email' variant='outlined' value={email} disabled={true} />
          <br />
          <TextField label='Password' type='password' variant='outlined' value={password} onChange={e => setPassword(e.target.value)} />
          <br />
          <div className="row">
            <div />
            <PrimaryButton variant='contained' onClick={handleResetPassword}>
              Reset Password
            </PrimaryButton>
          </div>
        </Box>
      </LoginContainer>
    )

  return (
    <>
      <ResetContainer>
        <Box
          sx={{ display: 'flex', flexDirection: 'column', marginRight: '40px' }}
          noValidate
          autoComplete='off'
        >
          <img className='logo' src={FLlogo} alt="Frontline Logo" />
          <h1>Optimization Platform</h1>
          Find the best sequence of activities with
          optimal resource loading to ace your project
        </Box>
        <Box className='form' component='form' noValidate autoComplete='off'>
          <h3>Reset Password</h3>
          <p className='error'>{errorMessage}</p>
          <p>Enter the email associated with your account and we'll send an email with instructions to reset your password.</p>
          <TextField label='Email' placeholder="example@gmail.com" variant='outlined' value={email} onChange={e => setEmail(e.target.value)} />
          <div className="row">
            <SecondaryButton variant='contained' onClick={handleBackPage}>
              Back
            </SecondaryButton>
            <PrimaryButton variant='contained' onClick={handleRequestResetPassword}>
              Request
            </PrimaryButton>
          </div>
        </Box>
      </ResetContainer>
    </>
  )
}