import React, { useState } from 'react'
// import FileDownloadIcon from '@mui/icons-material/FileDownload'

import Modal from 'src/feat/Modal'

import Table from '../../../components/Table'
import ResourcePlot from '../../../components/Plot/ResourcePlot'
import LossPlot from '../../../components/Plot/Loss'
import StepsPlot from '../../../components/Plot/Step'

import { plot_color_palette } from '../../../utils/color'

import { TableContent } from './styled'
import { OptimizationContainer } from '../InputData/styled'
import { OptimizationCharts } from '../OptimizedSummary/styled'
import { PrimaryButton, SecondaryButton } from 'src/styled'

export default function DetailedReport({ optimizer, selectedPreset, selectedResultIndex, handleSelectResultIndex, goPrev }) {
  const [openModal, setOpenModal] = useState(false)
  const data = optimizer?.results[selectedResultIndex]

  // current selected results
  const selectedTasksResult = data?.inp?.tasks
  const selectedResourcesResult = data?.inp?.rsrcs
  const selectedTasksResourcesResult = data?.inp?.task_rsrcs

  // filtered changed tasks
  const changed_tasks = selectedTasksResult && Object.keys(selectedTasksResult).filter(t => selectedTasksResult[t].changed)
  const tasks_rows =
    changed_tasks && changed_tasks.map((task) => {
      const { critical, id, task_code, name, drtn_hr, drtn_hr_2 } = selectedTasksResult[task]
      const calcRatio = (drtn_hr_2 / drtn_hr).toFixed(2)
      return ({
        id: task_code ? task_code : id,
        critical: critical,
        name: name,
        task_code: task_code ? task_code : id,
        old_duration: `${drtn_hr} hours`,
        new_duration: `${drtn_hr_2} hours`,
        ratio: calcRatio
      })
    })
  const tasks_columns = [
    {
      field: 'critical', headerName: 'Critical', width: 80, renderCell: (params) => {
        return params.value ? (
          <div className='circle critical' />
        ) : (
          <div className='circle' />
        )
      },
    },
    { field: 'id', headerName: 'ID', width: 280, },
    {
      field: 'name', headerName: 'Name', width: 430, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'old_duration', headerName: 'Duration (Baseline)', width: 150 },
    { field: 'new_duration', headerName: 'Duration (New)', width: 150 },
    { field: 'ratio', headerName: 'Ratio', width: 50 },
  ]

  // filtered changed resources
  const changed_resources = selectedResourcesResult && Object.keys(selectedResourcesResult)
  const resources_rows =
    changed_resources && changed_resources.map((resources) => {
      const { id, name, type, rng, dist, max, std } = selectedResourcesResult[resources]
      return ({
        name: name,
        id: id,
        type: type,
        rng: JSON.stringify(rng),
        dist: Math.round(Object.values(dist).reduce((total, current) => total + current, 0)),
        max: Math.round(max),
        std: Math.round(std)
      })
    })
  const resources_columns = [
    { field: 'id', headerName: 'ID', width: 120, },
    { field: 'type', headerName: 'type', width: 150, },
    {
      field: 'name', headerName: 'Name', width: 350, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'rng', headerName: 'Range [min, max]', width: 130 },
    { field: 'dist', headerName: 'Resource Dist.', width: 130 },
    { field: 'max', headerName: 'Max N.', width: 130 },
    { field: 'std', headerName: 'Standard Deviation', width: 130 },
  ]

  // filtered task-resources relationships
  const filteredTasksResourcesResults = []
  const changed_tasks_resources = selectedTasksResourcesResult && Object.keys(selectedTasksResourcesResult)
  changed_tasks_resources && changed_tasks_resources.map((rsrc) => {
    const arrayResources = optimizer?.results[selectedResultIndex]?.inp?.task_rsrcs[rsrc]
    changed_tasks.map((task) => {
      const mapTask = optimizer?.results[selectedResultIndex]?.inp?.tasks[task]
      if (arrayResources) {
        if (arrayResources.t_id === task) {
          filteredTasksResourcesResults.push({
            drtn_hr: mapTask.drtn_hr,
            drtn_hr_2: mapTask.drtn_hr_2,
            r_id: arrayResources.r_id,
            t_id: arrayResources.t_id,
            units: arrayResources.units,
            units_2: arrayResources.units_2,
            units_rng: arrayResources.units_rng,
            work_hr: arrayResources.work_hr
          })
        }
      }
    })
  })
  const hidden_columns = { id: false }
  const tasks_resources_rows = filteredTasksResourcesResults &&
    filteredTasksResourcesResults.map((resources, idx) => {
      const { task_code, r_id, t_id, units, units_2, units_rng, drtn_hr, drtn_hr_2, work_hr } = resources
      const checkCritical = (id) => {
        return (Object.values(data?.crt_path).filter((item) => item === id).length > 0)
      }
      return ({
        critical: checkCritical(t_id),
        id: idx,
        t_id: task_code ? task_code : t_id,
        resource_name: selectedResourcesResult[r_id]?.name,
        t_name: selectedTasksResult[t_id]?.name,
        old_units: units.toFixed(2),
        new_units: units_2.toFixed(2),
        rng: JSON.stringify(units_rng),
        old_duration: `${drtn_hr} hours`,
        new_duration: `${drtn_hr_2} hours`,
        work_hr: `${work_hr} hours`,
      })
    })
  const tasks_resources_columns = [
    {
      field: 'critical', headerName: 'Critical', width: 80, renderCell: (params) => {
        return params.value ? (
          <div className='circle critical' />
        ) : (
          <div className='circle' />
        )
      },
    },
    {
      field: 'id', headerName: 'ID', width: 0
    },
    { field: 't_id', headerName: 'Task Code', width: 120, },
    {
      field: 'resource_name', headerName: 'Resource Name', width: 200, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    {
      field: 't_name', headerName: 'Task Name', width: 350, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'rng', headerName: 'Range [min, max]', width: 130 },
    { field: 'old_units', headerName: 'Units (Old)', width: 130 },
    { field: 'new_units', headerName: 'Units (New)', width: 130 },
    { field: 'old_duration', headerName: 'Duration (Old)', width: 130 },
    { field: 'new_duration', headerName: 'Duration (New)', width: 130 },
    { field: 'work_hr', headerName: 'Total Work Hours', width: 130 },
  ]

  return (<>
    <div className="row">
      <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Detailed Report</h2>
      <PrimaryButton onClick={() => setOpenModal(true)} variant='contained'>
        Export Report
      </PrimaryButton>
    </div>
    <OptimizationContainer>
      <TableContent>
        <div className='row'>
          <h3>Changed Tasks (Overview)</h3>
          {/* <div><FileDownloadIcon /> Export</div> */}
        </div>
        <div className='legend' style={{ width: '400px', justifyContent: 'flex-start', marginBottom: '20px' }}>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginRight: '10px' }}>
            <div className='circle critical' /> Critical
          </div>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
            <div className='circle' /> Non-Critical
          </div>
        </div>
        {optimizer?.results[selectedResultIndex] ?
          <Table rows={tasks_rows} columns={tasks_columns} /> : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      <h2>Resources Tables</h2>
      <TableContent>
        <h4>Changed Resources</h4>
        {optimizer?.results[selectedResultIndex] ?
          <Table rows={resources_rows} columns={resources_columns} />
          : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      <TableContent>
        <h4>Changed Task-Resources</h4>
        {optimizer?.results[selectedResultIndex] ?
          <Table hidden_columns={hidden_columns} rows={tasks_resources_rows} columns={tasks_resources_columns} /> : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      {optimizer?.loss_weights ? <>
        <OptimizationCharts className='multi-charts'>
          <div className='row'><h2>Optimization Iteration Charts</h2></div>
          <div className='chart'>
            <h4>Project Duration</h4>
            <StepsPlot results={optimizer?.results} name='drtn_day' x_axis x_label='Optimization steps' y_label='Duration (days)' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Maximum Labour Resources</h4>
            <StepsPlot results={optimizer?.results} name='rsrc_max' x_axis x_label='Optimization steps' y_label='Maximum Resources' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Standard Deviation Labour Resources</h4>
            <StepsPlot results={optimizer?.results} name='rsrc_std' x_axis x_label='Optimization steps' y_label='Standard Deviation' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Number of Changed Tasks</h4>
            <StepsPlot results={optimizer?.results} name='num_changed' x_axis x_label='Optimization steps' y_label='Number of changed task' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
          <div className='chart'>
            <h4>Critical Path Tasks</h4>
            <StepsPlot results={optimizer?.results} name='num_crt' x_axis x_label='Optimization steps' y_label='Number of tasks' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
          <div className='chart'>
            <h4>Loss Function (Dimensionless)</h4>
            <StepsPlot results={optimizer?.results} name='loss_sum' x_axis x_label='Optimization steps' y_label='Loss function' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
        </OptimizationCharts>
        <OptimizationCharts className='multi-charts'>
          <div className='row'><h2>Resources Plots</h2></div>
          {Object.values(optimizer?.results[selectedResultIndex]?.inp.rsrcs || {}).map((r, i) => (
            <div key={r.id} className='chart'>
              <h4>{`(${r.type}) ${r.name}`}</h4>
              <ResourcePlot dist={r.dist} title={`(${r.type}) ${r.name}`} color={plot_color_palette.ResourcePlot.allResources[i % plot_color_palette.ResourcePlot.allResources.length]} />
            </div>
          ))}
        </OptimizationCharts>
        <OptimizationCharts className='multi-charts'>
          <div className='row'><h2>Loss Function Charts</h2></div>
          {/* <p>Loss functions in construction schedules are mathematical models used to determine the cost or schedule impact of deviations from the project plan. These functions help to identify potential project risks and allow for proactive measures to be taken to minimize the impact of delays and budget overruns. Common examples include cost-impact and schedule-impact functions.</p> */}
          <div className='chart'>
            <h4>Loss Function: Tasks Resources</h4>
            <LossPlot results={optimizer?.results} name='loss_task_rsrc' x_label='Duration (days)' y_label='Loss function: task resources' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
          <div className='chart'>
            <h4>Loss Function: Sum</h4>
            <LossPlot results={optimizer?.results} name='loss_sum' x_label='Duration (days)' y_label='Loss function: sum' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
          <div className='chart'>
            <h4>Loss Function: Resources</h4>
            <LossPlot results={optimizer?.results} name='loss_rsrc' x_label='Duration (days)' y_label='Loss function: resources' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
          <div className='chart'>
            <h4>Loss Function: Tasks</h4>
            <LossPlot results={optimizer?.results} name='loss_task' x_label='Duration (days)' y_label='Loss function: task' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>
        </OptimizationCharts>
      </> : 'No data'}

      <div className='row'>
        <SecondaryButton variant='contained' onClick={goPrev}>
          Back
        </SecondaryButton>
        <PrimaryButton onClick={() => setOpenModal(true)} variant='contained'>
          Export Report
        </PrimaryButton>
      </div>
    </OptimizationContainer>

    <Modal isLoading={openModal} onClose={() => setOpenModal(false)} title="Export Optimized File" content={<div className='row'>
      <PrimaryButton onClick={() => {
        optimizer.reader.merge_data(optimizer.results[selectedResultIndex].inp)
        optimizer.reader.export()
      }} variant='contained'>Original Format</PrimaryButton> &nbsp;
      <PrimaryButton onClick={() => {
        optimizer.reader.export_excel(optimizer.results[selectedResultIndex].inp)
      }} variant='contained'>Excel</PrimaryButton>
    </div>} />
  </>
  )
}