import React, { useState, useEffect } from 'react'

import { Optimizer } from './optimizers/Optimizer'
import { MspReader } from './readers/MspReader'
import { XerReader } from './readers/XerReader'

import { Slider, FormControl, Tooltip, Box } from '@mui/material'
import InfoIcon from '@mui/icons-material/Info'
import UploadFileIcon from '@mui/icons-material/UploadFile'

import Modal from 'src/feat/Modal'

import { PrimaryButton } from 'src/styled'
import { Content, OptimizationContainer, InputField } from './styled'

const ratioMarks = [{
  value: 0,
  label: '0%',
},
{
  value: 100,
  label: '100%',
},
{
  value: 200,
  label: '200%',
},]

const stepMarks = [{
  value: 0,
  label: '0',
},
{
  value: 50,
  label: '50',
},
{
  value: 100,
  label: '100',
},]

export default function InputData({ uploadedFile, setUploadedFile, fileName, setFileName, constraintsFileName, setConstraintsFileName, project, updateProject, goNext }) {
  const [isLoading, setIsLoading] = useState(false)
  const [isFileUploaded, setIsFileUploaded] = useState(project?.fileReader ? true : false)
  const [ratio, setRatio] = useState(project?.optimizer?.sim_in ? [Math.ceil(project.optimizer.sim_in['min_ratio'] * 100), Math.ceil(project.optimizer.sim_in['max_ratio'] * 100)] : [50, 150])
  const [steps, setSteps] = useState(project?.optimizer?.sim_in ? project.optimizer.sim_in['steps'] : 20)

  const todaysDate = new Date()
  const [minStartDate, setMinStartDate] = useState(-1)
  const [date, setDate] = useState(project?.optimizer?.sim_in ? new Date(project?.optimizer?.sim_in['default_start']).toISOString().slice(0, 10) : todaysDate.toISOString().slice(0, 10))

  useEffect(() => {
    if (project?.optimizer?.sim_in)
      project.optimizer.sim_in['default_start'] = date
    updateProject(project)
  }, [date])

  useEffect(() => {
    if (project?.optimizer?.sim_in)
      project.optimizer.sim_in['steps'] = steps
    updateProject(project)
  }, [steps])

  useEffect(() => {
    if (project?.optimizer?.sim_in) {
      project.optimizer.sim_in['min_ratio'] = ratio[0] / 100
      project.optimizer.sim_in['max_ratio'] = ratio[1] / 100
    }
    updateProject(project)
  }, [ratio])

  const readProject = (file) => {
    let reader
    if (file?.name.endsWith('xer'))
      reader = new XerReader()
    else if (file?.name.endsWith('xml'))
      reader = new MspReader()

    const checkIsDemo = fileName === 'demo_project.xml'

    reader.read_from_file(file).then(async (x) => {
      let min_start = -1
      Object.values(reader.data.tasks).forEach((t) => {
        if (min_start === -1 || min_start > t.start)
          min_start = t.start
      })
      setMinStartDate(min_start)
      const opt = new Optimizer(reader)
      opt.sim_in.default_start = min_start
      opt.sim_in.steps = 1
      if (opt.init) { await opt.init() }
      opt.optimize(() => { }, (opt_res) => {
        setFileName(file.name)
        setIsLoading(false)
        opt.baseline_result = opt_res[0]
        opt.sim_in.steps = steps
        Object.keys(reader.data.rsrcs).forEach(r => reader.data.rsrcs[r].baseline_resource = opt_res[0].inp.rsrcs[r].max)
        updateProject({ fileReader: reader, optimizer: opt, isDemo: checkIsDemo })
        setIsFileUploaded(true)
      })
    })
  }

  useEffect(() => {
    if (uploadedFile) {
      readProject(uploadedFile)
    }
  }, [uploadedFile])

  async function readConstraints(file) {
    if (!project?.fileReader)
      return
    setConstraintsFileName(file.name)
    await project.fileReader.read_constraints(file)

    const tasks = project.fileReader.data.tasks
    let ignoredTasks = []
    project.fileReader.cons_data.tasks_cons.forEach((c) => {
      let t = tasks[c.id]
      t.drtn_hr_rng = [c.min_drtn_hr, c.max_drtn_hr]
      if (c.ignored?.toUpperCase() === 'YES' || c.ignored?.toUpperCase() === 'TRUE' || c.ignored?.toUpperCase() === 'IGNORED') {
        ignoredTasks.push(c.id)
      }
    })

    let ignoredIndex = 0
    while (ignoredIndex < ignoredTasks.length) {
      const it = ignoredTasks[ignoredIndex]
      Object.values(tasks).forEach((t) => {
        for (const p in t.preds) {
          if (ignoredTasks.indexOf(p) > -1 && ignoredTasks.indexOf(t.id) === -1) {
            ignoredTasks.push(t.id)
            break
          }
        }
      })
      ignoredIndex++
    }

    Object.values(tasks).forEach((t) => {
      if (ignoredTasks.indexOf(t.id) > -1)
        t.ignored = true
    })

    updateProject(project)
  }

  function useDemoProject() {
    fetch('/demo_project.xml')
      .then((r) => r.blob())
      .then((r) => {
        const file = new File([r], 'demo_project.xml')
        // readProject(file, true)
        setUploadedFile(file)
      })
  }

  return <Content>
    <h2>Upload schedule and enter optimization settings</h2>
    <OptimizationContainer>
      <div className='optimization-input'>
        <div className='section'>
          <div className='intro'>
            <h2>Schedule</h2>
            <h3>Upload your schedule (.xer or .xml)</h3>
          </div>
          <div className='input-wrapper'>
            <div className='inputs' style={{ alignItems: 'center' }}>
              <div className='upload-file'>
                <input hidden type='file' id='project-file' name='project' onChange={(e) => {
                  setUploadedFile(e.target.files[0])
                  setIsLoading(true)
                }} accept='.xer,.xml' />
                <label className='upload-button' htmlFor='project-file'>
                  <UploadFileIcon /> Upload file
                </label>
                <span>{fileName}</span>
              </div>
              <span>or <span style={{ textDecoration: 'underline', cursor: 'pointer' }} onClick={useDemoProject}> use a demo project</span></span>
            </div>
          </div>
        </div>
        <div className={`section ${isFileUploaded ? '' : 'disabled'}`}>
          <div className='intro'>
            <h2>Constraints</h2>
            <h3>Upload your constraints (<span style={{ textDecoration: 'underline', cursor: 'pointer' }} onClick={() => { project.fileReader.export_constraints() }}>download template</span>)</h3>
          </div>
          <div className='input-wrapper'>
            <div className='inputs'>
              <div className='upload-file'>
                <input hidden type='file' id='constraints-file' name='constraints' onChange={(e) => { readConstraints(e.target.files[0]) }} accept='.xls,.xlsx' />
                <label className='upload-button  second-upload' htmlFor='constraints-file'>
                  <UploadFileIcon /> Upload file
                </label>
                <span>{constraintsFileName}</span>
              </div>
            </div>
          </div>
        </div>
        <div className={`section ${isFileUploaded ? '' : 'disabled'}`}>
          <div className='intro'>
            <h2>Optimization Settings</h2>
            <h3>Customize your optimization results</h3>
          </div>
          <div className='input-wrapper'>
            <div className='inputs'>
              <FormControl>
                <label>Default Start Date</label>
                <InputField
                  type='date'
                  inputProps={{
                    min: (new Date(minStartDate)).toISOString().slice(0, 10),
                  }}
                  defaultValue={date}
                  onChange={(e) => setDate(e.target.value)}
                  id='start-date'
                  placeholder='eg: 2023-10-19'
                  variant='outlined'
                  disabled={!isFileUploaded}
                />
                <small>Please enter commencement date</small>
              </FormControl>
              <FormControl>
                <label>Learning Rate <Tooltip title={<div style={{ fontSize: '13px', fontFamily: 'Inter' }}>The learning rate is a tuning parameter in an optimization algorithm that determines the step size at each iteration while moving toward a minimum of a loss function.</div>}><InfoIcon /></Tooltip></label>
                <InputField
                  id='start-date'
                  defaultValue={0.1}
                  placeholder='eg: 0.005'
                  variant='outlined'
                  disabled={!isFileUploaded}
                />
              </FormControl>
            </div>
            {/* Min & Max (Optimization Ratio) needs to have a range increments: 5% to 200% */}
            <div className='inputs'>
              <FormControl style={{ width: '100%' }}>
                <label>Optimization Ratio {ratio ? <>({ratio[0].toString()}% - {ratio[1].toString()}%)</> : ''}
                  <Tooltip title={<div style={{ fontSize: '13px', fontFamily: 'Inter' }}>Ratio = New Duration over Baseline Duration</div>}><InfoIcon /></Tooltip>
                </label>
                <Slider
                  id='optimization-ratio'
                  step={5}
                  min={0}
                  max={200}
                  valueLabelDisplay='auto'
                  getAriaLabel={() => 'Optimization Ratio'}
                  value={ratio}
                  marks={ratioMarks}
                  onChange={(e) => setRatio(e.target.value)}
                  disabled={!isFileUploaded}
                />
              </FormControl>
            </div>
            <div className='inputs'>
              <FormControl style={{ width: '100%' }}>
                <label>Optimization Steps ({steps.toString()})</label>
                <Slider
                  id='optimization-steps'
                  aria-label='optimization-steps'
                  value={steps}
                  onChange={(e) => setSteps(e.target.value)}
                  step={1}
                  min={10}
                  max={50}
                  marks={stepMarks}
                  valueLabelDisplay='auto'
                  disabled={!isFileUploaded}
                />
              </FormControl>
            </div>
          </div>
        </div>
      </div>
      <div className='row'>
        <div />
        <PrimaryButton variant='contained' onClick={goNext}
          disabled={!isFileUploaded} >
          Next
        </PrimaryButton>
      </div>

      <Modal isLoading={isLoading} title="Uploading Data" content="Please wait. This action may take up to a few minutes depending on the size of your file." spinner />
    </OptimizationContainer>
  </Content>
}