import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3, domains_with_margin } from '../../utils/plot'

export default function DistibutionPlot({
  results = [],
  optimizer,
  optimizationPresets,
  selectedPreset,
  selectedResultIndex = -1,
  width = 550, height = 500,
  k, x_label, y_label, onClick
}) {
  const ref = useRef()

  useEffect(() => {
    if (!results?.length) return null

    let res = [optimizer.baseline_result]
    Object.values(optimizationPresets).forEach((p) => {
      if (p.optimizer.results.length > 0)
        res = res.concat(p.optimizer.results)
    })

    const x_domain = d3.extent(res.map((d) => d.drtn_day))
    const y_domain = d3.extent(res.map((d) => d[k]))
    const domains = domains_with_margin(x_domain, y_domain, 0.15, 0.15)

    const { svg, x_scale, y_scale } = init_d3({
      x_domain: domains.x_domain,
      y_domain: domains.y_domain,
      margin: 50,
      width, height, ref,
      x_label,
      y_label,
      x_type: d3.scaleLinear,
      y_type: d3.scaleLinear,
    })

    const circleBaseline = svg.append('g')
    circleBaseline.selectAll('circle').data([optimizer.baseline_result]).join('circle')
      .attr('cx', d => x_scale(d.drtn_day))
      .attr('cy', d => y_scale(d[k]))
      .attr('r', d => 6)
      .attr('fill', '#cccccc')


    Object.keys(optimizationPresets).forEach((preset) => {
      const o = optimizationPresets[preset]
      const circle = svg.append('g')

      for (const r in o.optimizer.results)
        o.optimizer.results[r].index = parseInt(r)

      circle.selectAll('circle').data(o.optimizer.results).join('circle')
        .attr('cx', d => x_scale(d.drtn_day))
        .attr('cy', d => y_scale(d[k]))
        .attr('r', (d, i) => {
          if (i === 0) return 0
          return i === o.optimizer.sim_in.steps - 1 ? 6 : 2
        })
        .attr('fill-opacity', (d, i) => i / o.optimizer.results.length)
        .style('stroke', (d, i) => selectedResultIndex === d.index && selectedPreset === preset ? '#e61717' : undefined)
        .attr('stroke-width', (d, i) => selectedResultIndex === d.index && selectedPreset === preset ? 2 : 0)
        .attr('fill', o.color)
        .on('click', (e, d) => {
          onClick(preset, d.index)
        })
        .style('cursor', 'pointer')
    })
  }, [results])

  if (!results?.length) return null

  return (
    <svg ref={ref}>
    </svg>
  )
}