import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3 } from '../../utils/plot'
import { ms_per_day, len, down_sample } from '../../mods/a_utils'

export default function ResourcePlot({
  dist,
  color,
  width = 560,
  height = 500,
  keep = 1000,
}) {
  const ref = useRef()
  useEffect(() => {
    if (!dist) return

    const data = down_sample(Object.entries(dist), keep)
    // const color = d3.scaleOrdinal(d3.schemeCategory10)

    const x_domain = d3.extent(Object.keys(dist))
    // console.log('Before', x_domain)
    const y_domain = d3.extent(Object.values(dist))
    // const scale = d3.scaleLinear(x_domain, [40,width-40])
    // x_domain[1] = parseFloat(x_domain[1]) + 10000*(scale(ms_per_day) - scale(0))
    // console.log('After', x_domain, scale(ms_per_day) - scale(0))

    const { svg, x_scale, y_scale } = init_d3({
      x_domain,
      y_domain,
      width,
      height,
      ref,
      x_label: 'time',
      y_label: 'units',
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
    })

    const rect = svg.append('g')

    rect
      .selectAll('rect')
      .data(data)
      .join('rect')
      .attr('x', (d) => x_scale(d[0]))
      .attr('width', x_scale(ms_per_day) - x_scale(0))
      .attr('y', (d) => y_scale(d[1]))
      .attr('height', (d) => y_scale(y_domain[0]) - y_scale(d[1]))
      .attr('fill', color)
    // .style('fill', (d) => color(d[0]))

    // let legend = svg.append('g')
    //   .attr('transform', 'translate(0, 20)')

    // legend.selectAll('rect')
    //   .data(data)
    //   .join('rect')
    //   .attr('x', 0)
    //   .attr('y', (d, i) => i * 20)
    //   .attr('width', 19)
    //   .attr('height', 18)
    //   .style('fill', (d) => color(d[0]))

    // legend.selectAll('text')
    //   .data(data)
    //   .join('text')
    //   .attr('x', 22)
    //   .attr('y', (d, i) => i * 20 + 14)
    //   .text(d => {
    //     return data.name
    //   })

  }, [dist])

  if (!dist) return null
  const msg =
    len(dist) > keep
      ? `(showing ${keep}/${len(dist)} only due to limited pixels)`
      : ''
  return (
    <>
      {/* <Title>{title}</Title> */}
      <>{msg}</>
      <svg ref={ref}></svg>
    </>
  )
}