import React from 'react'
import { useNavigate } from 'react-router-dom'
import { useSignOut } from 'react-auth-kit'

import Menu from '@mui/material/Menu'
import MenuList from '@mui/material/MenuList'
import MenuItem from '@mui/material/MenuItem'
import ListItemText from '@mui/material/ListItemText'
import ListItemIcon from '@mui/material/ListItemIcon'
import Divider from '@mui/material/Divider'

import { HeaderContainer } from './styled'

import FLlogo from '../../assets/images/logo.png'

import MenuIcon from '@mui/icons-material/Menu'
import LaptopMacIcon from '@mui/icons-material/LaptopMac'
import LogoutIcon from '@mui/icons-material/Logout'

export default function Header() {
  const signOut = useSignOut()
  const navigate = useNavigate()

  function handleLogout() {
    signOut()
    // Might put some delay here (3 secs?)
    // alert('You have been logged out. You will be redirected to the login page.')
    navigate('/login')
  }

  const [anchorEl, setAnchorEl] = React.useState(null)
  const open = Boolean(anchorEl)
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  return <HeaderContainer>
    <div className='header'>
      <div className='logo'>
        <a href='/'><img src={FLlogo} alt='Frontline Logo' /></a>
        <span>v2.0.0</span>
      </div>
      <div className='account'>
        <MenuIcon
          id='basic-button'
          aria-controls={open ? 'basic-menu' : undefined}
          aria-haspopup='true'
          aria-expanded={open ? 'true' : undefined}
          onClick={handleClick} fontSize='medium' />
        <Menu
          id='basic-menu'
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          MenuListProps={{
            'aria-labelledby': 'basic-button',
          }}>
          <MenuList className='menu'>
            <MenuItem
              onClick={() => {
                window.open('https://app.frontline-optimizer.com/v1.0.0/', '_blank', 'noopener,noreferrer')
              }} >
              <ListItemIcon>
                <LaptopMacIcon fontSize='small' />
              </ListItemIcon>
              <ListItemText>Back to v1.0.0</ListItemText>
            </MenuItem>
            <Divider />
            <MenuItem onClick={handleLogout} >
              <ListItemIcon>
                <LogoutIcon />
              </ListItemIcon>
              <ListItemText>Log Out</ListItemText>
            </MenuItem>
          </MenuList>
        </Menu>
      </div>
    </div>
  </HeaderContainer>
}
