import React from 'react'

import { DataGrid } from '@mui/x-data-grid'

import { TableContainer } from './styled'

export default function Table({ rows, columns, hidden_columns }) {
  const makeRowHeight = (rows.length > 3) ? 500 : 300
  const initial_state = {
    columns: {
      columnVisibilityModel: hidden_columns,
    },
  }
  return <TableContainer>
    <div style={{
      height: makeRowHeight, width: '100%'
    }}>
      <DataGrid
        rows={rows}
        columns={columns}
        initialState={initial_state}
      // getRowId={(row) => row.id + row.name + row.t_name}
      />
    </div>
  </TableContainer>
}