import { XMLBuilder, XMLParser } from 'fast-xml-parser'
import {
  parse_drtn_hr,
  recover_drtn,
  parse_time,
  to_iso,
  ms_per_hr,
  encode_id,
  write_file,
  as_array,
} from '../../../../utils/reader'
import { ProjectReader } from './ProjectReader'

/**
 * https://learn.microsoft.com/en-us/office-project/xml-data-interchange/project-elements-and-xml-structure?view=project-client-2016
 */

export class MspReader extends ProjectReader {
  parse_text(text = '') {
    let parser = new XMLParser({
      ignoreAttributes: false,
    })
    this.raw_data = parser.parse(text)
  }

  extract_data() {
    let tasks = {}
    let rsrcs = {}
    let clndrs = {
      'id_-1': {
        id: 'id_-1',
        name: 'standard 5x(4+4)',
        week_hr_cnt: 40,
      },
    }

    let prj = this.raw_data.Project

    let all_tasks = {}
    prj.Tasks.Task.forEach((t) => {
      let id = encode_id(t.UID)
      let is_summary = t.Summary
      let name = t.Name
      all_tasks[id] = { is_summary, name }
    })

    prj.Tasks.Task.forEach((t) => {
      let id = encode_id(t.UID)
      let name = t.Name
      let start = Date.parse(t.Start + 'Z')
      let finish = Date.parse(t.Finish + 'Z')
      let drtn_hr = parse_drtn_hr(t.Duration)
      let act_start = Date.parse(t.ActualStart + 'Z')
      let act_finish = Date.parse(t.ActualFinish + 'Z')
      let clndr_id = encode_id(t.CalendarUID)

      

      let preds = {}
      as_array(t.PredecessorLink).forEach((x) => {
        let p_id = encode_id(x.PredecessorUID)
        let type = { 0: 'FF', 1: 'FS', 2: 'SF', 3: 'SS' }[x.Type]
        let lag_hr = x.LinkLag / 600
        let p = all_tasks[p_id]
        if (p.is_summary) {
          let msg = `ignoring RELATIONSHIP between \`${p.name}\` and \`${name}\` because the former is a summary`
          this.errors.add(msg)
        } else {
          preds[p_id] = {
            id: id + '-' + p_id,
            t_id: id,
            p_id,
            type,
            lag_hr,
          }
        }
      })

      if (all_tasks[id].is_summary) {
        //let msg = `ignoring TASK [${name}] because it is a summary`
      } else {
        tasks[id] = {
          id,
          name,
          start,
          finish,
          drtn_hr,
          act_start,
          act_finish,
          preds,
          rsrcs: {},
          clndr_id
        }
      }
    })

    as_array(prj.Resources.Resource).forEach((r) => {
      let id = encode_id(r.UID)
      let name = r.Name
      let type = ['Material', 'Work', 'Cost'][r.Type]
      type = { Work: 'Labor', Material: 'Material', Cost: 'Cost' }[type]
      rsrcs[id] = { id, name, type, dist: {} }
    })

    prj.Calendars.Calendar.forEach((c) => {
      let id = encode_id(c.UID)
      let name = c.Name

      let week_hr_cnt = 0

      if (c.WeekDays) {
        c.WeekDays.WeekDay.forEach((d) => {
          let day_name = [null, 'Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][
            d.DayType
          ]
          if (!day_name) return

          as_array(d.WorkingTimes?.WorkingTime).forEach((t) => {
            let x = parse_time(t.ToTime) - parse_time(t.FromTime)
            let hr = x / ms_per_hr
            if (hr < 0) hr += 24
            week_hr_cnt += hr
          })
        })
      }

      clndrs[id] = { id, name, week_hr_cnt }
    })

    if (prj.Assignments?.Assignment)
      prj.Assignments.Assignment.forEach((x) => {
        if (!x.Work) return
        let id = encode_id(x.UID)
        let t_id = encode_id(x.TaskUID)
        let r_id = encode_id(x.ResourceUID)
        if (!rsrcs[r_id]) {
          this.errors.add(`unknown resource \`${r_id}\` in resource assignment`)
          return
        }
        let work_hr = parse_drtn_hr(x.Work)
        let units = work_hr / tasks[t_id].drtn_hr

        /*let units_2 = parseFloat(x.Units)
      let units_3 = Math.round(parseFloat(x.PeekUnits))
      console.log(units === units_3)*/

        tasks[t_id].rsrcs[r_id] = {
          id,
          t_id,
          r_id,
          work_hr,
          units,
        }
      })

    this.data = { tasks, rsrcs, clndrs }
  }

  merge_data(inp) {
    let prj = this.raw_data.Project

    prj.Tasks.Task.forEach((t) => {
      if (t.Summary) return
      let id = encode_id(t.UID)
      let t2 = inp.tasks[id]

      t.Start = t.EarlyStart = t.LateStart = to_iso(t2.start_2)
      t.Finish = t.EarlyFinish = t.LateFinish = to_iso(t2.finish_2)
      t.Duration = recover_drtn(t2.drtn_hr_2)
      if (t.act_start && t.act_finish) {
        t.ActualDuration = t.Duration
      }
    })

    prj.Assignments.Assignment.forEach((x) => {
      if (!x.Work) return
      let id = encode_id(x.UID)
      let x2 = inp.task_rsrcs[id]
      if (!x2) return
      x.PeekUnits = x2.units_2
    })
  }

  export() {
    let builder = new XMLBuilder({
      ignoreAttributes: false,
    })
    let text = builder.build(this.raw_data)
    let name = this.filename.replace('.xml', '_Frontline.xml')
    write_file(name, text)
  }
}